import React from 'react'
import { Layout, Stack, Main } from '@layout'
import PageTitle from '@components/PageTitle'
import Divider from '@components/Divider'
import Seo from '@widgets/Seo'
import AuthorExpanded from '@widgets/AuthorExpanded'
import { useBlogAuthors } from '@helpers-blog'

const PageAuthors = props => {
  const exclude = ["Amber Wright", "Rocio Esteban"]
  const authors = useBlogAuthors().filter(
    author => !exclude.includes(author.name) )

  return (
    <Layout {...props}>
      <Seo title='Our Team' />
      <Divider />
      <Stack effectProps={{ effect: 'fadeInDown' }}>
        <PageTitle
          header='Team Members'
          subheader='EPI2ME Labs products and software are created by the Customer Workflows group at Oxford Nanopore Technologies. We have a range of experience and backgrounds.'
        />
      </Stack>
      <Stack>
        <Main>
          {authors
            .map(author => (
            <>
              <Divider />
              <AuthorExpanded author={author} withLink />
            </>
          ))}
        </Main>
      </Stack>
    </Layout>
  )
}

export default PageAuthors
